<template>
<div class="ect-container">
  <div class="header">
    <div class="sidebar-nav">
      <ul class="menu">
        <li v-for="(item,index) in sidebarNav" v-cloak>
          <span class="ts-2" :class="{active:item.isActive}" v-show="item.isShow" @click="clickTab(index)">{{ item.title }}</span>
          <div class="sub-menu" :class="{active:item.isActive}" v-show="item.isShow">
            <ul v-if="item.ectGridList">
              <draggable :list="item.ectGridList" :options="{group:{ name:'people',  pull:'clone', put:false }}">
                <li v-for="itemGridList in item.ectGridList" v-show="itemGridList.isShow">
                  <div class="ect-draggable">
                    <div class="ect-grid" :class="itemGridList.class"></div>
                  </div>
                </li>
              </draggable>
            </ul>
            <ul v-else>
              <draggable :options="{group:{ name:'people', pull:'clone', put:false }}" @start="onDarStart($event)">
                <li class="other" v-for="itemConList in item.ectConList" :data-module="itemConList.module" v-show="itemConList.isShow">
                  <div class="ect-draggable">
                    <i class="iconfont" :class="itemConList.icon"></i>
                    <span class="sub-menu-span" v-cloak>{{ itemConList.text }}</span>
                  </div>
                </li>
              </draggable>
            </ul>
          </div>
        </li>
      </ul>
    </div>
    <div class="right-nav">
      <ul>
        <li @click="previewMod()">
          <i class="iconfont" :class="rightNav[0].icon"></i>
          <span v-cloak>{{ rightNav[0].text }}</span>
        </li>
        <li>
          <i class="iconfont" :class="rightNav[1].icon"></i>
          <span v-cloak>{{ rightNav[1].text }}</span>
        </li>
        <li @click="saveMod()">
          <i class="iconfont" :class="rightNav[2].icon"></i>
          <span v-cloak>{{ rightNav[2].text }}</span>
        </li>
      </ul>
    </div>
  </div>
  <div class="ect-con">
    <div class="phone-view">
      <div class="phone-view-title">
        <h4>店铺首页</h4>
      </div>
      <div class="phone-view-con">
        <div id="visual-sortable">
          <draggable :list="modules" :options="{group:'people',handle: '.handle'}" @add="onVisAdd($event)" @move="onVisMove($event)" @start="onVisStart($event)" @end="onVisEnd($event)">
            <div class="div-component" v-for="(item, index) in modules" :class="{active: item.setting}" @click="isShowBackend(item.id,index)">
              <div class="handle"></div>
              <span class="remove-module" @click="removeMod(index,$event)">删除</span>
              <div class="actions"></div>
              <component :is="'ec-'+item.module" :id="item.id" :setting="item.setting" :data="item.data" :dialogTableVisible="dialogVisible" :is-visual="true" :ref="item.id"></component>
            </div>
          </draggable>
        </div>
      </div>
    </div>
  </div>
  <ec-gallery></ec-gallery>
  <div class="preview" v-if="preview">
    <div class="preview-close" @click="closePreview()">
        <i class="iconfont icon-close"></i>
    </div>
    <div class="preview-con">
      <div class="div-component" v-for="(item, index) in previewModules">
        <component :is="'ec-'+item.module" :id="item.id" :setting="false" :data="item.data" :dialogTableVisible="dialogVisible" :is-visual="false"></component>
    </div>
    </div>
  <div class="preview-mask"></div>
  </div>
</div>
</template>

<script>
// 模块组件
import eventHub from '../../eventHub.js'
import EcAnnouncement from '../../frontend/components/Announcement.vue'
import EcBlank from '../../frontend/components/Blank.vue'
import EcButton from '../../frontend/components/Button.vue'
import EcCoupon from '../../frontend/components/Coupon.vue'
import EcEditor from '../../frontend/components/Editor.vue'
import EcGroup from '../../frontend/components/Group.vue'
import EcImage from '../../frontend/components/Image.vue'
import EcLine from '../../frontend/components/Line.vue'
import EcLink from '../../frontend/components/Link.vue'
import EcList from '../../frontend/components/List.vue'
import EcNav from '../../frontend/components/Nav.vue'
import EcPicture from '../../frontend/components/Picture.vue'
import EcProduct from '../../frontend/components/Product.vue'
import EcSale from '../../frontend/components/Sale.vue'
import EcSearch from '../../frontend/components/Search.vue'
import EcSpike from '../../frontend/components/Spike.vue'
import EcTitle from '../../frontend/components/Title.vue'
import EcTabdown from '../../frontend/components/Tabdown.vue'
import EcGallery from '../../frontend/components/Gallery.vue'
import {
  Dialog,
  Message,
  Button
} from 'element-ui';


// 第三方组件
import axios from 'axios'
import store from '../../assets/js/store.min.js'
import draggable from 'vuedraggable'

// 模块配置
import tabbarData from '../../config/tabbar.js'
import toolsData from '../../config/tools.js'
import moduleData from '../../config/module.js'

export default {
  name: "visual",
  prop: ["id", "data", "setting",],
  components: {
    draggable,
    EcAnnouncement,
    EcBlank,
    EcButton,
    EcEditor,
    EcGroup,
    EcImage,
    EcLine,
    EcLink,
    EcList,
    EcNav,
    EcPicture,
    EcProduct,
    EcSale,
    EcSearch,
    EcSpike,
    EcTitle,
    EcTabdown,
    "EcDialog": Dialog,
    "EcButton": Button,
    Message,
    EcGallery
  },
  data() {
    return {
      "sidebarNav": [],
      "rightNav": [],
      "modules": [],
      "dialogVisible": false,
      "selectNum": 1,
      "preview":false,
      "previewModules":[]
    }
  },
  created() {
    eventHub.$on("message-show", this.messageShow);
  },
  beforeMount() {
    // 初始化页面数据
    this.sidebarNav = tabbarData; // tab导航
    this.rightNav = toolsData; // 保存按钮
    this.modules = moduleData; // 模块组件
  },
  mounted() {
    // console.log(this.$children.find(children => children.options.group == 'people').draggable);
    
  },
  methods: {
    closePreview(){
      this.preview = false;
      $("body").removeClass("hidden-x");
    },
    previewMod(){
      this.preview = true;
      this.previewModules = JSON.parse(JSON.stringify(this.modules));
      $("body").addClass("hidden-x");
    },
    messageShow(msg, type) {
      Message({
        showClose: true,
        message: msg,
        type: type
      });
    },
    onDarStart(evt) {

    },
    onVisMove(evt) {

    },
    onVisAdd(evt) { //排序列表添加
      let id = evt.newIndex;
      $(evt.item).remove();
      let moduleName = $(evt.item).data("module");
      let module = this.cloneModule(moduleName);
      this.modules.splice(id, 0, module);
    },
    onVisStart(evt) {
      let evtItem = $(evt.item);
      evtItem.css({
        "visibility": "hidden"
      });
    },
    onVisEnd(evt) {
      $(evt.item).css("visibility", "visible");
    },
    cloneModule(moduleName) { //克隆模块
      let id = moduleName + '-' + this.formatDate("ymdhis", new Date());
      delete require.cache[require.resolve(`../../config/${moduleName}`)];
      let config = require(`../../config/${moduleName}`);
      return {
        "id": id,
        "module": moduleName,
        "setting": false,
        "data": config
      }
    },
    formatDate(formatStr, fdate) { //格式化时间
      var fTime, fStr = 'ymdhis';
      if (!formatStr)
        formatStr = "y-m-d h:i:s";
      if (fdate)
        fTime = new Date(fdate);
      else
        fTime = new Date();
      var formatArr = [
        fTime.getFullYear().toString(),
        (fTime.getMonth() + 1).toString(),
        fTime.getDate().toString(),
        fTime.getHours().toString(),
        fTime.getMinutes().toString(),
        fTime.getSeconds().toString()
      ]
      for (var i = 0; i < formatArr.length; i++) {
        formatStr = formatStr.replace(fStr.charAt(i), formatArr[i]);
      }
      return formatStr;
    },
    getModIndex(id) { //获取当前组件索引
      let modulesIndex = 0;
      for (let i = 0; i < this.modules.length; i++) {
        if (this.modules[i].id == id) {
          modulesIndex = i;
          break;
        }
      }
      return modulesIndex;
    },
    isShowBackend(id, index) { //显示隐藏组件边框及操作
      let modules = this.modules;
      let modIndex = this.getModIndex(id);
      modules.map((v, i) => {
        i == index ? v.setting = true : v.setting = false;
      })
    },
    clickTab(index) { //点击导航切换
      if (this.sidebarNav[index].isActive) {
        this.sidebarNav[index].isActive = false;
      } else {
        this.sidebarNav.map(function(v, i) {
          i == index ? v.isActive = true : v.isActive = false;
        });
      }
    },
    saveMod() { //本地存储
      store.set('ectDivModules', this.modules)
    },
    removeMod(index, event) { //删除组件
      let modules = this.modules;
      modules.splice(index, 1);
      event.stopPropagation();
    }
  }
}
</script>
<style lang="scss">@import '../../assets/sass/visual.scss';
@import '../../assets/sass/var.scss';
body.hidden-x{overflow:hidden}
.div-component {
    position: relative;
    background: #F6F6F9;
}

.div-component .handle {
    position: absolute;
    left: 0;
    bottom: 0;
    right: 0;
    top: 0;
    z-index: 3;
}
.div-component.active .actions,
.div-component.active .remove-module,
.div-component:hover .actions,
.div-component:hover .remove-module {
    display: block;
    cursor: pointer;
}

.div-component .actions {
    border: 2px dashed #27b9d7;
    box-sizing: border-box;
    position: absolute;
    left: 0;
    bottom: 0;
    right: 0;
    top: 0;
    z-index: 2;
    display: none;

}

.div-component .remove-module {
    background: #27b9d7;
    color: #fff;
    font-size: 1.3rem;
    padding: 0.3rem 0.6rem;
    position: absolute;
    right: 0;
    bottom: 0;
    z-index: 8;
    display: none;
}

#visual-sortable .ect-draggable {
    padding: 1rem 1.5rem;
    box-sizing: content-box;
    background: #faf9f0;
}

.pirce-dialog {
    max-width: 880px;
    min-width: 880px;
    min-height: 500px;
    max-height: 600px;
    overflow-x: scroll;
}
.pirce-dialog .el-dialog__header {
    padding-bottom: 20px;
    border-bottom: 1px solid #F6F6F9;
}
.pirce-dialog .price-dialog-icon {
    overflow: hidden;
}
.pirce-dialog .price-dialog-icon li {
    width: 86px;
    height: 86px;
    float: left;
    margin: 9px;
    background: #f1f1f1;
    padding: 10px;
    position: relative;
}
.pirce-dialog .price-dialog-icon li img {
    max-width: 90%;
}
.pirce-dialog .price-dialog-icon li figure {
    text-align: center;
}
.pirce-dialog .price-dialog-icon li figcaption {
    font-size: 13px;
    color: #fff;
    position: absolute;
    background: rgba(0,0,0,.4);
    left: 0;
    bottom: 0;
    right: 0;
    padding: 4px 0;
}

.preview{position:fixed; z-index: 18; left:0; top:0; right:0; bottom:0; }
.preview .preview-close{font-size:18px; position:absolute; right:38px; top:38px; z-index: 16}
.preview .preview-close i{font-size:26px; color:#c0ccda;}
.preview .preview-close{cursor: pointer}
.preview .preview-close:hover i{color:#20a0ff}
.preview .div-component{max-width: 320px;}
.preview-con{width:320px; max-width: 320px; height:640px; overflow-x:scroll; position:absolute; top:10%; left:50%; transform: translateX(-50%); box-shadow: 0 0 16px #aaa; z-index:19}
.preview-mask{left:0; top:0; bottom:0; right:0; position:absolute; background:rgba(255,255,255,1);}
</style>



